/*start width*/
.main,
.main-section,
.overlay-section,
.mainthead,
.animation,
.animation ul {
  width: 100%;
}
/*start color*/
.main-section,
.manaeshTable,
.scoundtable,
.mainthead,
.table-title,
.Order {
  color: #fff;
}
/*start background-color*/
.main-section h1,
.manaeshTable h2,
.manaeshTable .Header,
.table-title,
.Order {
  background: #f78916;
}
/*start font-family*/
.main-section h1,
.manaeshTable th,
.manaeshTable tr,
.scoundtable,
.table-title {
  font-family: sans-serif;
}
/*start display*/

.main-section {
  height: 400px;
  margin: 0 auto;
  background-size: cover;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999999999;
}
.overlay-section {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background: black;
  opacity: 0.7;
  z-index: 1;
}
.main-section h1 {
  z-index: 999;
  font-weight: bold;
  font-size: 4rem;
  padding: 3%;
  text-align: center;
}

/*****************start table******************/
.manaeshTable {
  width: 80%;
  border-radius: 7px;
  margin: 0 auto;
  margin-top: 3%;
  padding: 3%;
  text-align: center;
  padding: 0px 3% 0% 0%;
}
.manaeshTable .Header {
  text-align: center;
  font-size: 2rem;
}
.manaeshTable th {
  font-weight: 400;
  font-size: 2rem;
  letter-spacing: 1.3px;
  width: 18%;
}
.manaeshTable tr {
  font-size: 1.2rem;
}
.manaeshTable td {
  widows: 27%;
  text-align: center;
}
.thtable {
  width: 18%;
  font-size: 1.5rem;
  font-weight: 350;
}
.scoundtable {
  width: 80%;
  border-radius: 7px;
  margin: 0 auto;
  padding: 1%;
  text-align: center;
  padding: 0px 3% 0% 0%;
}
.maintable {
  display: block;
}
.mainthead {
  font-size: 2rem;
  position: relative;
  display: inline;
  left: 50%;
  right: 50%;
}
.table-title {
  font-size: 2rem;
  width: 50%;
  margin: 0 auto;
  margin-bottom: 2%;
  text-align: center;
  margin-top: 5%;
}
.animation {
  position: fixed;
  height: 100%;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  z-index: -1;
  opacity: 0.9;
}
ul {
  position: absolute;
  bottom: -9%;
}
li {
  list-style: none;
  border-radius: 50%;
  display: inline-block;
  float: left;
  margin: 5% 5%;
  color: #f78916;
  position: relative;
  animation: 26s food linear infinite;
}
ul i {
  font-size: 50px;
  display: block;
  position: absolute;
}
ul li:nth-child(1) {
  bottom: -100px;
  animation-delay: 10s;
  animation-duration: 5s;
}
ul li:nth-child(2) {
  bottom: -800px;
  animation-delay: 3s;
}
ul li:nth-child(3) {
  bottom: -300px;
  animation-delay: 6s;
  animation-duration: 5s;
}
ul li:nth-child(4) {
  bottom: -270px;
  animation-delay: 10s;
}
ul li:nth-child(5) {
  bottom: -50px;
  animation-delay: 10s;
}
ul li:nth-child(6) {
  bottom: -150px;
  animation-delay: 5s;
}
ul li:nth-child(7) {
  bottom: -600px;
  animation-delay: 9s;
}
ul li:nth-child(8) {
  bottom: -400px;
  animation-delay: 1s;
}
ul li:nth-child(9) {
  bottom: -500px;
  animation-delay: 7s;
  animation-duration: 4s;
}

.Order {
  position: fixed;
  height: 70px;
  width: 70px;
  bottom: 2%;
  left: 2%;
  text-align: center;
  align-items: center;
  justify-content: center;
  display: flex;
  z-index: 999999999999;
  border-radius: 50%;
  box-shadow: 0px 0px 7px #fff;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.Order i {
  font-size: 3rem;
  color: #FFF;
}
.Order:hover {
  transform: scale(1.1);
  transition: 0.3s ease-in-out;
}

@keyframes food {
  from {
    transform: translateY(0) rotate(0deg);
  }
  to {
    transform: translateY(-1000px) rotate(360deg);
  }
}

/*start media*/
@media (min-width: 320px) and (max-width: 500px) {
  .main-section {
    height: 300px;
  }
  .main-section h1 {
    font-size: 3rem;
  }
  .manaeshTable {
    width: 100%;
    padding: 10% 0% 10% 0%;
  }
  .manaeshTable th {
    font-size: 1.6rem;
  }
  .scoundtable {
    padding: 0px 0% 5% 0%;
    width: 100%;
  }
  .mainthead {
    font-size: 1.5rem;
  }
  .table-title {
    width: 70%;
  }
  .Order {
    height: 50px;
    width: 50px;
  }
  .Order i {
    font-size: 2rem;
  }
}
@media (min-width: 500px) and (max-width: 720px) {
  .main-section {
    height: 300px;
  }
  .main-section h1 {
    font-size: 3rem;
  }
}
@media (min-width: 720px) and (max-width: 1111px) {
  .main-section {
    width: 100% !important ;
  }
}
